					"model2": this.state5,
				}).then(function(data) {
                       if(data.data.status == "-2") {
						this.$message.warning("If there is a problem with the data, please contact the administrator.");
					} else {

						var dataName = [];
						dataName = data.data.result.modelCommentList;
						// console.log(dataName);
						var tt = 0
						var list = [];
